function App() {

  function betsRenderer(bets:BitWallet[]){
    let localBets:any[] = [];
    for(let counter=0;counter<bets.length;++counter){
      localBets.push(betRenderer(bets[counter]));
    }
    return localBets;
  }

  function betRenderer(bet:BitWallet){
    let intensityValue = 0;
    if(bet.intensity === INTENSITY_TYPE.low){
      intensityValue = 0.01
    }else if(bet.intensity === INTENSITY_TYPE.medium){
      intensityValue = 0.05
    }else{
      intensityValue = 0.5
    }

    return(
      <div className={`bet-${bet.type}`}>
        {bet.qrcode? 
          <div className={"qrholder"} dangerouslySetInnerHTML={{__html:bet.qrcode}} />:
        null }
        <div className={"infoholder"}>
          <h1>
           Carteira: {bet.code}
          </h1>
          {/* tipo de aposta */}
          {bet.type === WALLET_TYPE.last?
          <div>
            Quem apostar por ultimo ganha 80% do valor da carteira
          </div>:null
          }

          {bet.type === WALLET_TYPE.most?
          <div>
            Quem apostar mais, ganha 80% do valor da carteira
          </div>:null
          }
          
          {bet.type === WALLET_TYPE.other?
          <div>
            Aposta relacionada a um numero de satoshi facilmente verificavel
          </div>:null
          }

          {/* ciclo de aposta */}
          {bet.cycle === CYCLE_TYPE.daily?
          <div>
            A aposta termina todos os dias as 23h59
          </div>:null
          }

          {bet.cycle === CYCLE_TYPE.weekly?
          <div>
            A aposta termina todas as segundas feiras as 23h59
          </div>:null
          }
          
          {bet.cycle === CYCLE_TYPE.monthly?
          <div>
            A aposta termina no ultimo dia do mes as 23h59
          </div>:null
          }
          
          {/* valor da aposta */}
          <div>
            valor minimo: {intensityValue} Bitcoins
          </div>
         
          <div className="betnow">
            <div className="betnowbutton">Apostar</div>
            <div className="betnowhidden">
            {bet.type === WALLET_TYPE.last?
          <div>
            Atenção, para que sua aposta seja válida você deve transferir um mínimo de {intensityValue} Bitcoins. Valores menores do que {intensityValue} serão desconsiderados e valores maiores do que {intensityValue} não aumentam sua chance de ganhar
          </div>:null
          }

          {bet.type === WALLET_TYPE.most?
          <div>
            Atenção, para que sua aposta seja válida você deve transferir um mínimo de {intensityValue} Bitcoins. Valores menores do que {intensityValue} serão desconsiderados
          </div>:null
          }
            
            </div>
          </div>
        </div>
       
      </div>
    )
  }
  
  return (
    <div className="App">
     {betsRenderer(BETS)}
    </div>
  );
}

export default App;
